import React from "react";
import { Link } from "gatsby";

import { NearByPost } from "../templates/post";

import { fetchPostName } from "../utils/urlHandler";

const PostPreNext = ({
  previous,
  next,
}: {
  previous: NearByPost | null;
  next: NearByPost | null;
}) => {
  return (
    <nav>
      <ul className="md:flex md:justify-between">
        <li className="flex grow overflow-hidden py-1 md:py-0">
          {previous && (
            <Link
              className="text-ellipsis whitespace-nowrap overflow-hidden"
              to={`/${fetchPostName(previous.fields.slug)}`}
              rel="prev"
            >
              ← {previous.frontmatter.title}
            </Link>
          )}
        </li>
        <li className="flex grow justify-end py-1 md:py-0">
          {next && (
            <Link
              className="text-ellipsis whitespace-nowrap overflow-hidden"
              to={`/${fetchPostName(next.fields.slug)}`}
              rel="next"
            >
              {next.frontmatter.title} →
            </Link>
          )}
        </li>
      </ul>
    </nav>
  );
};

export default PostPreNext;
